body {
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

#search-box {
  width: 300px;
  padding: 10px;
  font-size: 16px;
}

#countries-list {
  flex-wrap: wrap;
  margin-top: 20px;
  display: flex;
}

.country {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 200px;
  margin-bottom: 20px;
  margin-right: 20px;
  padding: 10px;
  box-shadow: 0 2px 4px #0000001a;
}

.flag {
  border-radius: 5px;
  width: 100%;
  margin-bottom: 10px;
}

.country-name {
  font-weight: bold;
}

